@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro&display=swap);
body,
html {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  display: flex;
  font-family: "Source Sans Pro", sans-serif;
  padding: 0px;
  margin: 0px;
  min-height: 100%;
  width: 100%;
  min-width: 300px;
}

body {
  background-color: rgba(135, 206, 250, 0.7);
}

p {
  color: rgb(255, 255, 255);
}

.Spinner > div {
  width: 18px;
  height: 18px;
  background-color: rgba(199, 21, 133, 0.6);
  border-radius: 100%;
  display: inline-block;
  -webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;
  animation: sk-bouncedelay 1.4s infinite ease-in-out both;
}

.Spinner .bounce1 {
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}

.Spinner .bounce2 {
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}

@-webkit-keyframes sk-bouncedelay {
  0%,
  80%,
  100% {
    -webkit-transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
  }
}

@keyframes sk-bouncedelay {
  0%,
  80%,
  100% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

.MySelect {
  display: flex;
  flex-direction: row;
  justify-content: center;
  box-sizing: border-box;
  font-size: 20px;
}

.select-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 70px;
  max-width: 300px;
  width: 100%;
  padding: 0 1em;
  font-size: 1em;
}

@media (min-width: 901px) {
  .select-container {
    max-width: 350px;
  }
}

.select-container label {
  font-size: 1em;
  width: inherit;
}

select,
select option {
  width: inherit;
  height: 40px;
  background-color: rgb(240, 248, 255);
  border: none;
  font-size: 1em;
  display: flex;
  justify-content: center;
}

select:focus {
  outline: none;
}

button {
  width: inherit;
  height: 40px;
  border: none;
  -webkit-transition: 0.5s all;
  transition: 0.5s all;
  margin-top: 5px;
  background-color: rgb(70, 130, 180);
  color: rgb(255, 255, 255);
  font-size: 1em;
}

button:focus {
  outline: none;
}

button:active {
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}

.Quote {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  text-align: center;
  width: 100%;
  max-width: 700px;
  padding: 0;
  margin: 50px auto;
  color: rgb(105, 105, 105);
}

.Quote p {
  color: rgb(105, 105, 105);
}

blockquote {
  line-height: 40px;
}

/* spinner */
.spinner {
  margin: 100px auto 0;
  width: 70px;
  text-align: center;
}
.App {
  display: flex;
  flex-direction: column;
  width: 100vw;
  min-height: 100%;
  text-align: center;
}
.App nav {
  background-color: rgb(240, 248, 255);
  height: 60px;
  color: rgb(70, 130, 180);
  display: flex;
  align-items: center;
  padding: 0px 20px;
  box-shadow: 0 2px 2px -2px rgba(0, 0, 0, 0.2);
}

.App nav h1 {
  border-left: 3px solid rgb(70, 130, 180);
  padding-left: 4px;
}

.App .app-instructions {
  text-align: center;
  width: 100%;
}

.App .app-instructions small {
  color: rgb(199, 21, 133);
}

.App .app-instructions .kwote {
  color: rgb(70, 130, 180);
}

footer {
  position: absolute;
  bottom: 0;
  margin: 5px;
  color: rgb(70, 130, 180);
}

